html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

button {
  padding: 10px 20px;
}

button:hover {
  opacity: 0.8;
}

input[type=text], input[type=password], input[type=number] {
  padding: 5px 15px;
  border: 1px solid #eeeeee;
}

[rel=default] {
  display: none;
}

.container {
  position: relative;
  width: 100%;
  height: 100vh;
}

.radio.have-children .radio {
  display: none;
}

.radio.have-children.is-show .radio {
  display: block;
}

.radio__title {
  position: relative;
  padding-left: 30px;
}

.radio .radio {
  margin-left: 30px;
  margin-top: 15px;
}

.radio input[type=radio] {
  display: none;
}

.radio label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 20px;
  width: 20px;
  border: 1px solid royalblue;
}

.radio label:after {
  display: none;
  content: '';
  position: absolute;
  left: 4px;
  top: 4px;
  height: 12px;
  width: 12px;
  background-color: royalblue;
}

h3.radio__title:before {
  content: '';
  position: absolute;
  left: 7px;
  top: 0;
  height: 20px;
  width: 6px;
  background-color: green;
}

h3.radio__title:after {
  content: '';
  position: absolute;
  left: 0;
  top: 7px;
  height: 6px;
  width: 20px;
  background-color: green;
}

.radio__input:checked + .radio__label:after {
  display: block;
}

.radio.have-children > .radio__input:checked + .radio__label:after {
  display: none;
}

.radio__input ~ .child {
  display: none;
}

.radio__input:checked ~ .child {
  display: block;
}

.auth-key {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.auth-key.is-show {
  display: block;
}

.auth-key__footer {
  display: flex;
}

.auth-key h3 {
  font-size: 14px;
  margin-bottom: 5px;
}

.auth-key select {
  margin-bottom: 15px;
}

.auth-key input[type=file] {
  margin-bottom: 15px;
}

.slection-key {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slection-key.is-hide {
  display: none;
}

.cabinet-user {
  display: none;
  padding: 20px;
}

.cabinet-user.is-show {
  display: block;
}

.form-room {
  display: none;
  position: absolute;
  top: 80px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.form-room.is-show {
  display: block;
}

.form-room__title {
  font-size: 18px;
  text-align: center;
  margin-bottom: 20px;
}

.form-room__subtitle {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 2px;
}

.form-room__btn {
  display: flex;
  justify-content: center;
}

.form-room__row {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.form-room__row-unit {
  display: flex;
}

.form-room__row-unit sup {
  font-size: 10px;
}

.form-room__col {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.tabs__nav-title {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  list-style-type: none;
  border-bottom: 1px solid #ddd;
}

.tabs__nav-title li {
  margin-right: 2px;
  margin-bottom: 2px;
}

.tabs__nav-title li a {
  display: inline-block;
  padding: 10px;
  background-color: #efefef;
  color: black;
}

.tabs__nav-title li.is-active a {
  background-color: #de7373;
}

.tabs__contents {
  display: flex;
  flex-direction: column;
}

.tabs__content {
  display: none;
}

.tabs__content.is-active {
  display: block;
  padding: 20px;
  width: 100%;
  min-height: 660px;
  background-color: #efefef;
}

.tabs__btns-nav {
  display: flex;
  justify-content: space-between;
}
